import React from "react";
import style from "./question5.module.css";
import { useNavigate } from "react-router-dom";
import foundationArrowLeft from "../../assets/foundation_arrow-up.svg";
import foundationArrowRight from "../../assets/Vector.svg";
import questImg5 from "../../assets/questImg5.svg";
import { useDispatch, useSelector } from "react-redux";
import {
  getPhysicaltrainingCheck,
  getPhysicaltrainingSelector,
} from "../../redux/slices/physicaltrainingCheckSlice";

const Question5 = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const physicaltrainingValue = useSelector(getPhysicaltrainingSelector);

  const physicaltrainingCheckHandler = (e) => {
    dispatch(getPhysicaltrainingCheck(e.target.value));
  };

  const goBackHandler = () => {
    navigate("/question4");
  };

  const goForwardHandler = () => {
    if (physicaltrainingValue) {
      navigate("/question6");
    }
    return;
  };

  return (
    <div className={style.container}>
      <div>
        <div className={style.container_wrap_p1}>
          <p className={style.p1}> 5/8</p>
        </div>
        <div className={style.container_textInput}>
          <p className={style.container_p} style={{ marginBottom: "20px" }}>
                 ?   
            <br />
               30     3   
            ?
          </p>
        </div>
        <div className={style.container_col_check}>
          <div className={style.container_col_check_men}>
            <div className={style.container_col_check_input}>
              <input
                value={0}
                onChange={physicaltrainingCheckHandler}
                type="radio"
                name="physicaltrainingCheck"
              />
              <p></p>
            </div>
            <div className={style.container_col_check_input}>
              <input
                value={2}
                onChange={physicaltrainingCheckHandler}
                type="radio"
                name="physicaltrainingCheck"
              />
              <p></p>
            </div>
          </div>
          <div className={style.container_col_check_men}>
            <div className={style.container_col_check_input}>
              <p>(0 )</p>
            </div>
            <div className={style.container_col_check_input}>
              <p>(2 )</p>
            </div>
          </div>
        </div>
        <div className={style.container_buttons}>
          <button
            onClick={goBackHandler}
            className={style.buttonBack}
            type="button"
          >
            <div className={style.buttonBack_wrap}>
              <img
                src={foundationArrowLeft}
                alt="foundationArrowUp"
                layout="intrinsic"
              />
              <p className={style.buttonBack_Text}></p>
            </div>
          </button>
          <button
            onClick={goForwardHandler}
            className={style.buttonForward}
            type="button"
          >
            <div className={style.buttonBack_wrap}>
              <p className={style.buttonFovard_Text}></p>
              <img
                src={foundationArrowRight}
                alt="foundationArrowUp"
                layout="intrinsic"
              />
            </div>
          </button>
        </div>
      </div>
      <div className={style.container_wrap_imgQuest1}>
        <img src={questImg5} alt="questImg5" layout="intrinsic" />
      </div>
    </div>
  );
};

export default Question5;
