import React from "react";
import style from "./calculate.module.css";
import { useNavigate } from "react-router-dom";
import diagram12 from "../../assets/diagram12.svg";
import _12 from "../../assets/_12.svg";
import diagram15_20 from "../../assets/diagram15_20.svg";
import _15_20 from "../../assets/1520.svg";
import diagram14 from "../../assets/diagram14.svg";
import _12_14 from "../../assets/_12_14.svg";
import diagram_20 from "../../assets/diagram_20.svg";
import _20 from "../../assets/_20.svg";

import foundationArrowLeft from "../../assets/foundation_arrow-up.svg";
import { useSelector } from "react-redux";
import { getTotalResultSelector } from "../../redux/slices/totalResultSlice";

const Calculate = () => {
  const navigate = useNavigate();

  const totalResult = useSelector(getTotalResultSelector);

  const goBackHandler = () => {
    if (totalResult < 7) {
      navigate("/result1");
    }
    if (totalResult >= 7 && totalResult <= 11) {
      navigate("/result2");
    }
    if (totalResult >= 12 && totalResult <= 14) {
      navigate("/result3");
    }
    if (totalResult >= 15 && totalResult <= 20) {
      navigate("/result4");
    }
    if (totalResult > 20) {
      navigate("/result5");
    }

    return;
  };

  return (
    <div className={style.container}>
      <div className={style.container_wrapTable}>
        <table className={style.container_wrapTable_table}>
          <tbody>
            <tr>
              <th className={style.th1}>  </th>
              <th className={style.th2}>   2 </th>
              <th className={style.th3}>   2 </th>
            </tr>
            <tr>
              <td> 7</td>
              <td className={style.td1_2}> </td>
              <td>1  100  1%</td>
            </tr>
            <tr>
              <td>711</td>
              <td className={style.td2_2}> </td>
              <td>1  25  4%</td>
            </tr>
            <tr>
              <td>1214</td>
              <td className={style.td3_2}></td>
              <td>1  6  17%</td>
            </tr>
            <tr>
              <td>1520</td>
              <td className={style.td4_2}></td>
              <td>1  3  33%</td>
            </tr>
            <tr>
              <td> 20</td>
              <td className={style.td5_2}> </td>
              <td>1  2  50%</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div className={style.container_wrapDiagram}>
        <div className={style.container_wrapDiagram_right}>
          <div
            className={style.container_wrapDiagram_right_up}
            style={{ marginBottom: "5px" }}
          >
            <div className={style.container_wrapDiagram_right_img}>
              <img
                style={{ marginTop: "10px" }}
                src={diagram12}
                alt="diagram12"
                layout="intrinsic"
              />
              <img
                style={{ width: "37px", height: "35px" }}
                src={_12}
                alt="_12"
                layout="intrinsic"
              />
            </div>
            <p style={{ marginLeft: "24px" }}>
                  12.  
              <br />
                . 
              <br />
                  !
            </p>
          </div>
          <div className={style.container_wrapDiagram_right_down}>
            <div className={style.container_wrapDiagram_right_img}>
              <img
                style={{ marginTop: "10px" }}
                src={diagram15_20}
                alt="diagram15_20"
                layout="intrinsic"
              />
              <img
                style={{ marginTop: "10px" }}
                src={_15_20}
                alt="_15_20"
                layout="intrinsic"
              />
            </div>
            <p style={{ marginLeft: "24px" }}>
                 1520. ,   
                  2 .   
               
              <br />
              ()  .     
                     
               
              <br />
                ()  .
            </p>
          </div>
        </div>
        <div className={style.container_wrapDiagram_right}>
          <div
            className={style.container_wrapDiagram_right_up}
            style={{ marginBottom: "5px" }}
          >
            <div className={style.container_wrapDiagram_right_img}>
              <img
                style={{ marginTop: "10px" }}
                src={diagram14}
                alt="diagram14"
                layout="intrinsic"
              />
              <img
                style={{ marginTop: "10px" }}
                src={_12_14}
                alt="_12_14"
                layout="intrinsic"
              />
            </div>
            <p style={{ marginLeft: "24px" }}>
                 1214. ,
              <br />   . 
              <br />
                .
            </p>
          </div>
          <div className={style.container_wrapDiagram_right_down}>
            <div className={style.container_wrapDiagram_right_img}>
              <img
                style={{ marginTop: "10px" }}
                src={diagram_20}
                alt="diagram_20"
                layout="intrinsic"
              />
              <img
                style={{ marginTop: "10px" }}
                src={_20}
                alt="_20"
                layout="intrinsic"
              />
            </div>
            <p style={{ marginLeft: "24px" }}>
                  20.  
              <br />
                   
              <br />2 .      ()
              <br />     . 
              <br />
                  
              <br />
                    
              <br />
                ()  .
            </p>
          </div>
        </div>
      </div>
      <div className={style.container_wrapButton}>
        <button
          onClick={goBackHandler}
          className={style.buttonBack}
          type="button"
        >
          <div className={style.buttonBack_wrap}>
            <img
              src={foundationArrowLeft}
              alt="foundationArrowUp"
              layout="intrinsic"
            />
            <p className={style.buttonBack_Text}></p>
          </div>
        </button>
      </div>
    </div>
  );
};

export default Calculate;
